<script>
  import {chartUtils} from '@/utils/chart.js'
  import {OutdoorTestApi} from '@/http/api/outdoorTest'
  export default {
    name: '',
    components: {


    },
    props:['rowData'],
    data() {
      return {
        rowClassName:(record,index)=>{
          let className = index % 2 ? 'shallow_gray': '';
          return className
        },
        countData:{wxzbBean:{},ftpzbBean:{},yyzbBean:{},pingzbBean:{}},

      }
    },
    beforeCreate() {

    },
    mounted() {
      this.initData();
    },
    methods: {
      async initData(){
        const params = {
          pid:this.rowData.id
        }
        const data = await OutdoorTestApi.queryAnalyzeData(params);
        this.countData=data.data.object;
        const rsrpLegend = [];
        const rsrpColor = [];
        const rsrpData = [];
        const sinrLegend = [];
        const sinrColor = [];
        const sinrData = [];
        const ulLegend = [];
        const ulColor = [];
        const ulData = [];
        const dlLegend = [];
        const dlColor = [];
        const dlData = [];
        if(data.data.success){
          data.data.object.range.map(x=>{
            if(x.item =='RSRP'){
              x.content.map(v=>{
                rsrpLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                rsrpColor.push(v.colorVal);
                rsrpData.push({
                  name:v.minVal+'~'+v.maxVal,
                  type: 'bar',
                  data: [v.num]
                });
              })
            }else if(x.item =='SINR'){
              x.content.map(v=>{
                sinrLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                sinrColor.push(v.colorVal);
                sinrData.push({
                  value: [v.num],
                  name:v.minVal+'~'+v.maxVal
                });
              })
            }else if(x.item =='UL'){
              x.content.map(v=>{
                ulLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                ulColor.push(v.colorVal);
                ulData.push({
                  value: [v.num],
                  name:v.minVal+'~'+v.maxVal
                });
              })
            }else{
              x.content.map(v=>{
                dlLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                dlColor.push(v.colorVal);
                dlData.push({
                  value: [v.num],
                  name:v.minVal+'~'+v.maxVal
                });
              })
            }
          })
        }
        chartUtils.initColumn("wireless1",rsrpLegend,'RSRP',rsrpColor,rsrpData);
        chartUtils.initPie("wireless2",sinrLegend,'SINR',sinrColor,sinrData);
        chartUtils.initPie("ftpchart1",ulLegend,'UL',ulColor,ulData);
        chartUtils.initPie("ftpchart2",dlLegend,'DL',dlColor,dlData);
        this.initKpis(data.data.object);
      },
      initKpis(data){
        const color = ['#51B1F9','#FB6669'];
        const volteLegend1 = ['',''];
        const volteData1 = [{value:data.CALLSUCC,name:''},{value:data.CALLFAIL,name:''}];
        let successRatio1 = '';
        if(data.CALLSUCC+data.CALLFAIL==0){
          successRatio1 = 0;
        }else{
          successRatio1 = (data.CALLSUCC / (data.CALLSUCC + data.CALLFAIL)).toFixed(2)*100
        }

        const volteLegend2 = ['', ''];
        const volteData2 = [{value:data.CALLSUCC,name:''},{value:data.CALLUNLINE,name:''}];
        let successRatio2 = '';
        if(data.CALLSUCC+data.CALLUNLINE==0){
          successRatio2 = 0;
        }else{
          successRatio2 = (data.CALLUNLINE / (data.CALLSUCC + data.CALLUNLINE)).toFixed(2)*100
        }

        const httpLegend1 = ['Ping', 'Ping'];
        const httpData1 = [{value:data.PINGSUCC,name:'Ping'},{value:data.PINGFAIL,name:'Ping'}];
        let httpRatio1 = '';
        if(data.PINGSUCC+data.PINGFAIL==0){
          httpRatio1 = 0;
        }else{
          httpRatio1 = (data.PINGSUCC / (data.PINGSUCC + data.PINGFAIL)).toFixed(2)*100
        }

        const httpLegend2 = ['Http', 'Http'];
        const httpData2 = [{value:data.HTTPSUCC,name:'Http'},{value:data.HTTPFAIL,name:'Http'}];
        let httpRatio2 = '';
        if(data.PINGSUCC+data.PINGFAIL==0){
          httpRatio2 = 0;
        }else{
          httpRatio2 = (data.HTTPSUCC / (data.HTTPSUCC + data.HTTPFAIL)).toFixed(2) *100
        }
        chartUtils.initPie("volteChart1",volteLegend1,''+successRatio1+'%',color,volteData1);
        chartUtils.initPie("volteChart2",volteLegend2,''+successRatio2+'%',color,volteData2);
        chartUtils.initPie("httpChart1",httpLegend1,'Ping'+httpRatio1+'%',color,httpData1);
        chartUtils.initPie("httpChart2",httpLegend2,'Http'+httpRatio2+'%',color,httpData2);

      }
    }
  }
</script>

<style>
  @import "../../../../static/css/style.css";

  .column_kpi{
    /*column-count:3;*/
    /*column-gap:5px;*/
    /*height: 35vh;*/
    /*column-rule-style:dotted;*/
    border-right: 1px solid #dddddd;
  }
  table {
    border-collapse: collapse;
  }
  table td {
    border:1px solid #dddddd;
    padding:5px 20px;
    text-align: left;
  }

</style>



